
.goods {
  position: absolute;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  top: 180px;
  bottom: 46px;
  overflow: hidden;
}
.goods .menu-wrapper {
  width: 80px;
  background: #f3f5f7;
}
.goods .menu-wrapper .menu-item {
  display: table;
  height: 54px;
  width: 56px;
  padding: 0 12px;
  line-height: 14px;
}
.goods .menu-wrapper.current {
  position: relative;
  z-index: 10;
  margin-top: -1px;
  background: #fff;
  font-weight: 700;
}
.goods .menu-wrapper .text {
  text-align: center;
}
.goods .menu-wrapper .icon {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
  margin-right: 2px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.goods .menu-wrapper .icon.decrease {
  background-image: url('../../components/goods/images/decrease_3@2x.png');
}
@media (min-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3) {
.goods .menu-wrapper .icon.decrease {
    background-image: url('../../components/goods/images/decrease_3@3x.png');
}
}
.goods .menu-wrapper .icon.discount {
  background-image: url('../../components/goods/images/discount_3@2x.png');
}
@media (min-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3) {
.goods .menu-wrapper .icon.discount {
    background-image: url('../../components/goods/images/discount_3@3x.png');
}
}
.goods .menu-wrapper .icon.guarantee {
  background-image: url('../../components/goods/images/guarantee_3@2x.png');
}
@media (min-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3) {
.goods .menu-wrapper .icon.guarantee {
    background-image: url('../../components/goods/images/guarantee_3@3x.png');
}
}
.goods .menu-wrapper .icon.invoice {
  background-image: url('../../components/goods/images/invoice_3@2x.png');
}
@media (min-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3) {
.goods .menu-wrapper .icon.invoice {
    background-image: url('../../components/goods/images/invoice_3@3x.png');
}
}
.goods .menu-wrapper .icon.special {
  background-image: url('../../components/goods/images/special_3@2x.png');
}
@media (min-device-pixel-ratio: 3), (-webkit-min-device-pixel-ratio: 3) {
.goods .menu-wrapper .icon.special {
    background-image: url('../../components/goods/images/special_3@3x.png');
}
}
.goods .menu-wrapper .text {
  display: table-cell;
  width: 56px;
  vertical-align: middle;
  position: relative;
  border: none;
  /* 1.5倍屏 */
  /* 2倍屏 */
  /* 3倍屏 */
  font-size: 12px;
}
.goods .menu-wrapper .text:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  background: rgba(7, 17, 27, 0.1);
  width: 100%;
  height: 1px;
}
@media only screen and (-webkit-min-device-pixel-ratio: 1.5) {
.goods .menu-wrapper .text .border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.goods .menu-wrapper .text .border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3) {
.goods .menu-wrapper .text .border-1px::after {
    -webkit-transform: scaleY(0.33);
    transform: scaleY(0.33);
}
}
.goods .foots-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}



